
.content{
    display: flex;
    z-index: 1;
    width: 100%;
    
}
.see{
    flex: 1 1 65%;
    background-color: #383838;
    height: 100vh;
}
.control{
    flex: 1 1 35%;
    background-color: #303030;
    height: 100vh;
}
.title{
    margin-top: 45px;
    margin-left: 45px;
    background-color: transparent;
}

.video{
    margin-left: 45px;
    margin-top: 5%;
}
.h1{
    font-family: 'Noto Serif Display', serif;
    font-weight: 200;
    font-style: italic;
    color: #ffffff;
    font-size: 50px;
    letter-spacing: -2.2px;    
    background: transparent; 
}
.explain{
    margin-top: 2%;
    margin-left: 50px;
    width: 45%;
    float: left;
}
.sideview{
    margin-top: 2%;
    width: 45%;
    float: right;
}
.p {
    font-weight: 200;
    color: #ffffff;
    font-size: 20px;
    letter-spacing: -0.8px;    
    background: transparent; 
}
.h3{
    font-family: 'Noto Serif Display', serif;
    font-weight: 100;
    color: #ffffff;
     font-size: 24px;
    letter-spacing: -0.3px;
    line-height: 1.2;
     text-align: right;
     background: transparent; 
}
.semititle{
    margin-top: 5%;
    margin-left: 45px;
}
.control_img{
    width: 40%;
}
.semititle{
    background-color: #303030;
}
.speed{
    background-color: #303030;
}

.controller{
    background-color: #303030;
    margin-left: 45px;
    float: left;
    width: 50%;
}
.button{
    width: 40%;
    float: right;
    background-color: #303030;
    margin-left: 3%;
}
.button:hover{
    cursor: pointer;
}
.bottom{
    margin-top: 3%;
}
.control_img{
    background-color: #303030;
    width: 100%;
}
.control_gif{
    width: 80%;
    border-style: solid;
    border-color: #ffffff;
    border-width: 0.3px;
}
.front_gif{
    width: 90%;
}
.button_img{
    background-color: #303030;
    width: 60%;
    background: transparent;
}
.button_img:hover{
    cursor: pointer;
    width: 62%;
}
.control_01{
    display: flex;
    align-items: flex-end;
    background-color: #303030;
    margin-top: 3%;
}