@import url(https://fonts.googleapis.com/css2?family=M+PLUS+2:wght@100;200;300;400;500;600;700&family=Tinos:ital,wght@1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+Display:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  background-color: #383838;
  padding: 0;
  font-family: 'M PLUS 2', serif;
}

.App {
  text-align: center;
}






.container {
    z-index: 1;
    width: 100%;
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
    margin-top: 1%;
  }

  .banner{
      display: flex;
      padding-right: 2%;
    padding-left: 2%;
      z-index: 1;
      width: 20vw;
      background-color: #1B1818;
      height: 100vh;
  }
  .nav{
    background-color: #1B1818;
  }
  .navitem{
    width: auto; /* 67px */
    height: auto; /* 33px */
    overflow: visible;
    white-space: pre;
    color: #ffffff;
    font-size: 28px;
    letter-spacing: -1.3px;
    line-height: 1.2;
    background-color: #1B1818;
  }
  .img{
      width: 90%;
      background-color: #1B1818;
  }

.content{
    display: flex;
    z-index: 1;
    width: 100%;
    
}
