.container {
    z-index: 1;
    width: 100%;
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
    margin-top: 1%;
  }

  .banner{
      display: flex;
      padding-right: 2%;
    padding-left: 2%;
      z-index: 1;
      width: 20vw;
      background-color: #1B1818;
      height: 100vh;
  }
  .nav{
    background-color: #1B1818;
  }
  .navitem{
    width: auto; /* 67px */
    height: auto; /* 33px */
    overflow: visible;
    white-space: pre;
    color: #ffffff;
    font-size: 28px;
    letter-spacing: -1.3px;
    line-height: 1.2;
    background-color: #1B1818;
  }
  .img{
      width: 90%;
      background-color: #1B1818;
  }